.button-error {
  background: #ca3c3c;
  /* this is a maroon */
}
.button-warning {
  background: #df7514;
  /* this is an orange */
}
.button-secondary {
  background: #42b8dd;
  /* this is a light blue */
}
.input-group {
  display: table;
  border-collapse: collapse;
  width: 100%;
}
.input-group > div {
  display: table-cell;
  border: 1px solid #ddd;
  vertical-align: middle;
  /* needed for Safari */
}
.input-group-icon {
  background: #0078e7;
  color: #fff;
  padding: 0 12px;
  cursor: pointer;
}
.input-group-area {
  width: 100%;
}
.input-group input[type=text],
.input-group select {
  display: block;
  width: 100%;
  padding: 8px;
  border: none;
  -webkit-box-shadow: none;
  box-shadow: none;
}
.dropdown {
  position: relative;
  display: inline-block;
}
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #fff;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  padding: 12px 16px;
  z-index: 1;
}
.dropdown.active .dropdown-content {
  display: block;
}
.dropdown.active .item {
  padding: 6px;
}
.table-container {
  background-color: #fff;
  padding: 20px;
  overflow-x: auto;
}
table {
  border-collapse: collapse;
  width: 100%;
}
th,
td {
  text-align: left;
  padding: 8px;
}
tr {
  cursor: pointer;
  border-bottom: 1px solid #ddd;
  font-size: 1rem;
}
tr:hover {
  background-color: #f5f5f5;
}
.pagination {
  display: inline-block;
  padding: 0;
  margin: 10px;
}
.pagination a {
  color: black;
  float: left;
  padding: 8px 16px;
  text-decoration: none;
  transition: background-color 0.3s;
  border-left: 1px solid #ddd;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
.pagination a:last-child {
  border-right: 1px solid #ddd;
}
.pagination a.active {
  background-color: #0078e7;
  color: white;
  border: 1px solid #0078e7;
}
.pagination a:hover:not(.active) {
  background-color: #ddd;
}
.pull-right {
  float: right;
}
.pull-left {
  float: left;
}
.paging select {
  margin-top: 11px;
  height: 34px;
  border-radius: 0px;
}
.v-select,
.v-select * {
  cursor: pointer;
}
.star {
  color: #f2b01e;
}
